import React, { Component } from 'react';
import Pagination from "react-js-pagination";
import { fetchUsers } from '../services/userService';
import UserTable from './userTable';

const usersPerPage = 3;

export default class Users extends Component {
	
	state = {
		total: 0,
		activePage: 1,
		users: [],
	}

	componentDidMount = () => {
		this.fetchUsers(null, 1);  
	}

	fetchUsers = (query, page) => {
		fetchUsers(query, page, usersPerPage)
		.then(req => req.json())
		.then((resp) => {
			if (resp.success) {
				const users = resp.users.map(x => ({
					name: x.name,
					last_name: x.last_name,
					email: x.email,
					age: x.age,
					gender: x.gender,
					_id: x._id.$oid
				}));
				this.setState(prevState => ({ ...prevState, users, total: resp.count }));
			}
		}).catch(err => {
			
		});
	}

	handlePageChange(activePage) {
		debugger;
		this.setState(prevState => ({ ...prevState, activePage }));
		this.fetchUsers(null, activePage);
  }

	render = () => {
		const { activePage, total, users } = this.state;
		return (
			<React.Fragment>
				<UserTable users={users} />
				<Pagination
					activePage={activePage}
					itemsCountPerPage={usersPerPage}
					totalItemsCount={total}
					pageRangeDisplayed={5}
					onChange={(activePage) => this.handlePageChange(activePage)}
					itemClass="page-item"
					activeClass="active"
					disabledClass="disabled"
					prevPageText="Anterior"
					nextPageText="Siguiente"
					linkClass="page-link"
				/>
			</React.Fragment>
		);
	}
}