const USERS_EP = 'http://127.0.0.1:5000/v1/users'

export const fetchUsers = (query, page, limit) => {
    let uri = USERS_EP;

    if (query) {
        uri += `?query=${query}`;
        if (page && limit) uri += '&';
    }

    if (page && limit) {
        uri += `?page=${page}&limit=${limit}`
    }

    return fetch(uri, {
        method: 'GET',
    });
}